// /app/page.tsx
"use client";

import React, { useState, useEffect, useRef } from "react";
import { 
    ContentSchema, 
    FormInputs, 
    MODES, 
    TONES, 
    DEFAULT_WORD_COUNTS, 
    Draft, 
    ContentMode 
} from "../components/types"; // Corrected relative path

// --- UTILITY COMPONENTS ---

interface ToastProps {
  message: string;
  type: "success" | "error";
  onClose: () => void;
}

const Toast: React.FC<ToastProps> = ({ message, type, onClose }) => {
  useEffect(() => {
    const timer = setTimeout(onClose, 4000);
    return () => clearTimeout(timer);
  }, [onClose]);

  const bgColor = type === "success" ? "bg-emerald-600" : "bg-red-600";
  const icon = type === "success" ? "✓" : "!";

  return (
    <div
      className={`fixed right-4 bottom-4 z-50 p-4 rounded-xl shadow-2xl text-white flex items-center space-x-3 ${bgColor}`}
    >
      <span className="text-xl font-bold">{icon}</span>
      <p>{message}</p>
      <button onClick={onClose} className="text-white opacity-70 hover:opacity-100">
        &times;
      </button>
    </div>
  );
};

const CopyButton: React.FC<{ content: string; onCopy: (msg: string) => void }> = ({ content, onCopy }) => {
  const handleCopy = () => {
    if (content.trim()) {
      // document.execCommand('copy') is used for broader compatibility in sandboxed environments
      const el = document.createElement('textarea');
      el.value = content;
      document.body.appendChild(el);
      el.select();
      document.execCommand('copy');
      document.body.removeChild(el);
      
      onCopy("Content copied to clipboard!");
    } else {
      onCopy("Nothing to copy.");
    }
  };

  return (
    <button
      onClick={handleCopy}
      type="button" // Important for buttons inside forms
      className="text-xs text-neutral-400 hover:text-emerald-500 transition-colors shrink-0"
      title="Copy to Clipboard"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        className="h-4 w-4 inline-block ml-1"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
        strokeWidth={2}
      >
        <path strokeLinecap="round" strokeLinejoin="round" d="M8 5H6a2 2 0 00-2 2v12a2 2 0 002 2h12a2 2 0 002-2v-2m-2-4l-4-4m-4 4l4 4" />
      </svg>
    </button>
  );
};

const SkeletonLoader: React.FC = () => (
  <div className="p-6">
    <div className="h-6 bg-neutral-700 rounded w-3/4 mb-4 animate-pulse"></div>
    <div className="h-4 bg-neutral-700 rounded w-1/2 mb-8 animate-pulse"></div>
    <div className="space-y-4">
      <div className="h-4 bg-neutral-700 rounded w-full animate-pulse"></div>
      <div className="h-4 bg-neutral-700 rounded w-5/6 animate-pulse"></div>
      <div className="h-4 bg-neutral-700 rounded w-full animate-pulse"></div>
      <div className="h-4 bg-neutral-700 rounded w-3/5 animate-pulse"></div>
    </div>
  </div>
);

// --- MAIN COMPONENT ---

export default function HomePage() {
  const [form, setForm] = useState<FormInputs>({
    topic_or_key_message: "",
    mode: "Blog",
    tone: "Professional/Neutral",
    word_count: DEFAULT_WORD_COUNTS["Blog"],
  });
  const [loading, setLoading] = useState(false);
  const [currentDraft, setCurrentDraft] = useState<ContentSchema | null>(null);
  const [draftHistory, setDraftHistory] = useState<Draft[]>([]);
  const [isHistoryOpen, setIsHistoryOpen] = useState(true);
  const [toast, setToast] = useState<{ message: string; type: "success" | "error" } | null>(null);

  const keyMessageRef = useRef<HTMLTextAreaElement>(null);

  // Helper to show toasts
  const showToast = (message: string, type: "success" | "error") => {
    setToast({ message, type });
  };

  // Handler for form input changes
  const onChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target as any;
    
    // If mode changes, reset word count to the new default
    if (name === "mode") {
      setForm((f) => ({
        ...f,
        [name]: value as ContentMode,
        word_count: DEFAULT_WORD_COUNTS[value as ContentMode],
      }));
    } else {
      setForm((f) => ({
        ...f,
        [name]: name === "word_count" && value !== "" ? Number(value) : value,
      }));
    }
  };

  // Handler for saving current result to history
  const handleNewDraft = (draft: ContentSchema) => {
    if (!draft) return;

    const newDraft: Draft = {
      ...draft,
      id: Date.now().toString(),
      mode: form.mode,
      date: new Date().toLocaleTimeString(),
      key_message: form.topic_or_key_message,
    };

    setDraftHistory((prev) => [newDraft, ...prev]);
    setCurrentDraft(null); // Clear main display
    showToast("Draft moved to Previous Drafts!", "success");
  };

  // Handler for re-loading history draft
  const loadDraft = (draft: Draft) => {
    setCurrentDraft(draft);
    setIsHistoryOpen(false);
    showToast(`Loaded draft from ${draft.date}`, "success");
  };
  
  // Handler for main form submission
  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setToast(null);
    
    // Simple front-end validation
    if (!form.topic_or_key_message.trim()) {
        showToast("Please provide a topic or key message.", "error");
        keyMessageRef.current?.focus();
        setLoading(false);
        return;
    }

    // Check for required Key Audience in WhitePaper mode
    const showKeyAudience = form.mode === "WhitePaper";
    if (showKeyAudience && !form.key_audience?.trim()) {
        showToast("Key Audience is required for White Paper mode.", "error");
        setLoading(false);
        return;
    }

    try {
      const res = await fetch("/api/generate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      const json = await res.json();
      
      if (!res.ok) {
          throw new Error(json?.error || "Generation failed due to a server error.");
      }

      setCurrentDraft(json as ContentSchema);
      showToast("Content generated successfully!", "success");

    } catch (err: any) {
      setCurrentDraft(null);
      showToast(`Generation Error: ${err.message}`, "error");
    } finally {
      setLoading(false);
    }
  };

  // --- RENDERING HELPERS ---

  const renderResultContent = (data: ContentSchema) => (
    <div className="space-y-5 p-6 bg-neutral-800 rounded-xl min-h-[300px]">
      {/* Title */}
      <div className="border-b border-neutral-700 pb-3">
        <h3 className="text-sm font-semibold uppercase text-neutral-400 mb-1">
          TITLE / SUBJECT LINE
        </h3>
        {data.title ? (
          <h1 className="text-2xl font-bold text-white flex justify-between items-start">
            {data.title}
            <CopyButton content={data.title} onCopy={showToast} />
          </h1>
        ) : (
          <h1 className="text-2xl font-bold text-neutral-500">
            LinkedIn Post (No Title)
          </h1>
        )}
      </div>

      {/* Meta/Hook */}
      {(data.meta_or_hook || form.mode === 'Email') && (
        <div className="border-b border-neutral-700 pb-3">
          <h3 className="text-sm font-semibold uppercase text-neutral-400 mb-1">
            {form.mode === 'Email' ? 'Email Meta (Subject Line above)' : 'SEO META DESCRIPTION / HOOK'}
          </h3>
          {data.meta_or_hook ? (
            <p className="text-neutral-300 flex justify-between items-start">
              <span className="max-w-[95%]">{data.meta_or_hook}</span>
              <CopyButton content={data.meta_or_hook} onCopy={showToast} />
            </p>
          ) : (
             <p className="text-neutral-500 italic">Field intentionally left empty for Email mode.</p>
          )}
        </div>
      )}

      {/* Main Body */}
      <div className="border-b border-neutral-700 pb-3">
        <h3 className="text-sm font-semibold uppercase text-neutral-400 mb-2">
          MAIN BODY CONTENT
        </h3>
        {data.main_body_sections.map((p, i) => (
          <p key={i} className="mb-3 text-neutral-200 leading-relaxed flex justify-between items-start">
            <span className="max-w-[95%]">{p}</span>
            <CopyButton content={p} onCopy={showToast} />
          </p>
        ))}
      </div>

      {/* CTA */}
      <div className="pt-3">
        <h3 className="text-sm font-semibold uppercase text-neutral-400 mb-2">
          CALL TO ACTION / CLOSING
        </h3>
        {data.cta_or_closing && (
          <p className="font-bold text-emerald-400 flex justify-between items-start">
            <span className="max-w-[95%]">{data.cta_or_closing}</span>
            <CopyButton content={data.cta_or_closing} onCopy={showToast} />
          </p>
        )}
      </div>
      
      {/* Disclaimer */}
      {data.disclaimer && (
        <div className="mt-4 p-3 rounded-lg bg-yellow-900/50 border border-yellow-800">
            <p className="text-xs text-yellow-300">{data.disclaimer}</p>
        </div>
      )}
      
      {/* New Draft Button */}
      <div className="pt-4 flex justify-end">
        <button
            onClick={() => handleNewDraft(data)}
            type="button"
            className="px-4 py-2 bg-neutral-700 text-white rounded-xl text-sm font-medium hover:bg-neutral-600 transition-colors"
        >
            New Draft → Move to History
        </button>
      </div>
    </div>
  );

  const currentModeLabel = MODES.find(m => m.id === form.mode)?.label || form.mode;
  const wordCountDisplay = form.word_count || DEFAULT_WORD_COUNTS[form.mode];
  const placeholderText = `Default for ${currentModeLabel}`;
  
  // Custom input conditional rendering for Extensibility
  const showKeyAudience = form.mode === "WhitePaper";
  
  return (
    <main className="min-h-screen bg-[#111827] text-white p-4 sm:p-8">
      <div className="mx-auto max-w-7xl">
        <h1 className="text-3xl font-bold mb-8">Content Assistant</h1>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          
          {/* LEFT COLUMN: INPUTS AND HISTORY */}
          <div className="lg:col-span-1 space-y-8">

            {/* Content Generation Inputs Card */}
            <div className="bg-neutral-800 p-6 rounded-xl shadow-lg">
              <h2 className="text-xl font-semibold mb-4 flex items-center text-emerald-400">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path><polyline points="14 2 14 8 20 8"></polyline><line x1="16" y1="13" x2="8" y2="13"></line><line x1="16" y1="17" x2="8" y2="17"></line><polyline points="10 9 9 9 8 9"></polyline></svg>
                Content Generation Inputs
              </h2>

              <form onSubmit={onSubmit} className="space-y-4">
                
                {/* Mode and Tone Selectors */}
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-neutral-300">Mode</label>
                    <select
                      name="mode"
                      value={form.mode}
                      onChange={onChange}
                      className="mt-1 w-full rounded-lg border border-neutral-700 bg-neutral-700 px-3 py-2 text-white focus:ring-emerald-500 focus:border-emerald-500"
                      required
                    >
                      {MODES.map((m) => (
                        <option key={m.id} value={m.id}>{m.label}</option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-neutral-300">Tone</label>
                    <select
                      name="tone"
                      value={form.tone}
                      onChange={onChange}
                      className="mt-1 w-full rounded-lg border border-neutral-700 bg-neutral-700 px-3 py-2 text-white focus:ring-emerald-500 focus:border-emerald-500"
                    >
                      {TONES.map((t) => (
                        <option key={t} value={t}>{t}</option>
                      ))}
                    </select>
                    <p className="mt-1 text-xs text-neutral-500">Default: Professional/Neutral</p>
                  </div>
                </div>

                {/* Key Audience Input (Conditional) */}
                {showKeyAudience && (
                    <div>
                        <label className="block text-sm font-medium text-neutral-300">Key Audience *</label>
                        <input
                          type="text"
                          name="key_audience"
                          value={form.key_audience || ""}
                          onChange={onChange}
                          required={showKeyAudience}
                          className="mt-1 w-full rounded-lg border border-neutral-700 bg-neutral-700 px-3 py-2 text-white focus:ring-emerald-500 focus:border-emerald-500"
                          placeholder="e.g., CTOs of Fortune 500 companies"
                        />
                         <p className="mt-1 text-xs text-neutral-500">Required for White Paper mode.</p>
                    </div>
                )}
                
                {/* Topic Input */}
                <div>
                  <label className="block text-sm font-medium text-neutral-300">Topic or Key Message *</label>
                  <textarea
                    ref={keyMessageRef}
                    name="topic_or_key_message"
                    value={form.topic_or_key_message}
                    onChange={onChange}
                    required
                    maxLength={500}
                    className="mt-1 w-full rounded-lg border border-neutral-700 bg-neutral-700 px-3 py-2 text-white focus:ring-emerald-500 focus:border-emerald-500"
                    rows={3}
                    placeholder="e.g., How AI assistants help small businesses publish more content"
                  />
                  <div className="mt-1 text-xs text-neutral-500 flex justify-between">
                    <span>{form.topic_or_key_message.length}/500</span>
                    <span className="text-neutral-400">Word Count (optional)</span>
                  </div>
                </div>

                {/* Word Count Input */}
                <div className="flex items-center space-x-4">
                    <input
                      type="number"
                      name="word_count"
                      value={form.word_count ?? ""}
                      onChange={onChange}
                      min={50}
                      step={50}
                      className="w-1/2 rounded-lg border border-neutral-700 bg-neutral-700 px-3 py-2 text-white focus:ring-emerald-500 focus:border-emerald-500"
                      placeholder={placeholderText}
                    />
                    <p className="text-xs text-neutral-500">
                        Uses mode default ({wordCountDisplay}) if empty.
                    </p>
                </div>

                <button
                  type="submit"
                  disabled={loading || !form.topic_or_key_message.trim() || (showKeyAudience && !form.key_audience?.trim())}
                  className={`mt-4 w-full rounded-lg px-4 py-2 text-white font-semibold transition-colors flex items-center justify-center space-x-2 
                    ${loading || !form.topic_or_key_message.trim() || (showKeyAudience && !form.key_audience?.trim()) ? 'bg-neutral-600 cursor-not-allowed' : 'bg-emerald-600 hover:bg-emerald-700'}`}
                >
                  {loading ? (
                    <>
                      <svg className="animate-spin h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle><path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>
                      <span>Generating Content...</span>
                    </>
                  ) : (
                    <span>Generate Content</span>
                  )}
                </button>
              </form>
            </div>
            
            {/* Previous Drafts Card (Collapsible) */}
            <div className="bg-neutral-800 rounded-xl shadow-lg overflow-hidden">
                <button 
                    onClick={() => setIsHistoryOpen(!isHistoryOpen)}
                    type="button"
                    className="w-full p-4 text-left text-xl font-semibold flex items-center justify-between hover:bg-neutral-700 transition-colors"
                >
                    <span className="flex items-center text-emerald-400">
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2z"></path><polyline points="12 6 12 12 16 14"></polyline></svg>
                      Previous Drafts ({draftHistory.length})
                    </span>
                    <svg 
                        className={`h-5 w-5 transform transition-transform ${isHistoryOpen ? 'rotate-180' : 'rotate-0'}`} 
                        xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                    >
                      <polyline points="6 9 12 15 18 9"></polyline>
                    </svg>
                </button>
                
                {isHistoryOpen && (
                    <div className="p-4 border-t border-neutral-700 space-y-3 max-h-96 overflow-y-auto">
                        {draftHistory.length === 0 ? (
                            <p className="text-neutral-400 italic">No drafts saved yet. Hit "New Draft" to save results!</p>
                        ) : (
                            draftHistory.map((draft) => (
                                <div 
                                    key={draft.id} 
                                    onClick={() => loadDraft(draft)}
                                    className="p-3 bg-neutral-700 rounded-lg cursor-pointer hover:bg-neutral-600 transition-colors"
                                >
                                    <p className="text-sm font-medium truncate">{draft.title || draft.meta_or_hook || draft.key_message}</p>
                                    <p className="text-xs text-neutral-400 mt-1">
                                        {draft.mode} | Generated at {draft.date}
                                    </p>
                                </div>
                            ))
                        )}
                    </div>
                )}
            </div>
          </div>
          
          {/* RIGHT COLUMN: GENERATED CONTENT */}
          <div className="lg:col-span-2">
            <h2 className="text-xl font-semibold mb-4 flex items-center text-emerald-400">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mr-2" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M2 3h6a4 4 0 0 1 4 4v14a3 3 0 0 0-3-3H2z"></path><path d="M22 3h-6a4 4 0 0 0-4 4v14a3 3 0 0 1 3-3h7z"></path></svg>
                Generated Content
            </h2>
            
            <div className="bg-neutral-800 rounded-xl shadow-lg">
              {loading && <SkeletonLoader />}
              {!loading && currentDraft && renderResultContent(currentDraft)}
              {!loading && !currentDraft && (
                <div className="p-12 text-center text-neutral-500">
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="1"><path strokeLinecap="round" strokeLinejoin="round" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" /></svg>
                  <p>No content generated yet.</p>
                  <p className="text-sm">Your AI-powered content will appear here.</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      
      {/* Toast Notification */}
      {toast && <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />}
    </main>
  );
}